   onAddPrimaryMember() {
    // this.service.initializeFormGroup();
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '50%';
    dialogConfig.height = '85%';
    dialogConfig.data = {member: new Member(new Tier()), action: 'save'};
    const dialogRef = this.dialog.open(MemberComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(
      () => {
        this.getMemberList();
      }
    );
  }

  onAddDependentMember() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '50%';
    dialogConfig.height = '85%';
    dialogConfig.data = {member: new Member(new Tier()), action: 'save', parentId: this.parentId};
    const dialogRef = this.dialog.open(MemberComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(
      () => {
        this.getMemberList();
      }
    );
  }

  onEdit(row: Member, action: string) {
    console.log(row);
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '50%';
    dialogConfig.height = '85%';
    dialogConfig.data = {member: row, action: 'update'};
    const dialogRef = this.dialog.open(MemberComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(
      () => {
        this.getMemberList();
      }
    );
  }

  onDelete(row: Member) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '30%';
    dialogConfig.height = '47%';
    dialogConfig.data = {
      displayWarning: 'Are you sure you want to delete?',
      subject: 'Member',
      subjectName: row.firstName + ' ' + row.middleName + ' ' + row.lastName,
      btnActionLabel: 'Delete Member'
    };
    const dialogRef = this.dialog.open(UserAuthorizationComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(result => {
      this.getMemberList();
      if (result === true) {
          console.log('delete member');
          this.alertify.success(
            'Church member \' ' + row.firstName + ' ' +
            row.middleName + ' ' + row.lastName +
            ' \' deleted successfully.'
          );
          //delete from DB active -> inactive
      } else if (result === false) {
        console.log('DON\'T delete member');
        this.alertify.error(
          'Invalid password and can\'t delete church member \'' +
          row.firstName + ' ' +
          row.middleName + ' ' +
          row.lastName + '\''
        );
      }

      });
  }

